<template>
  <div>
    <div v-if="!linkContent" class="note">
      在 {{ localUrl }} 後輸入任意字串，產生 link：
    </div>
    <div v-if="!linkContent" class="note-link">
      demo://test-link/${input_str}
    </div>
    <div class="note-link">{{ `${schema}${linkContent}` }}</div>
  </div>
</template>

<script>
export default {
  name: "Link",
  data() {
    return {
      schema: "demo://test-link/",
      linkContent: "",
      localUrl: "",
    };
  },
  created() {
    const url = new URL(window.location.href);
    this.localUrl = `${url.protocol}//${url.host}/#/link/`;

    this.linkContent = `${window.location.href}`.replace(this.localUrl, "");
    if (this.linkContent) {
      window.onload = () => {
        this.toAppLink();
      };
    }
  },
  methods: {
    toAppLink() {
      let redirectUrl = "";
      let timeout = 2000;

      const userAgent = navigator.userAgent.toLowerCase();
      if (/iphone|ipad|ipod/.test(userAgent)) {
        // iOS
        redirectUrl = "https://apps.apple.com/tw/app/國泰人壽/id432046643";
        timeout = timeout * 2;
      } else if (/android/.test(userAgent)) {
        // Android
        redirectUrl =
          "https://play.google.com/store/apps/details?id=com.cathay.main&hl=zh_TW&gl=US";
      } else {
        // PC/NB
        // redirectUrl =
        //   "https://online.cathaylife.com.tw/OIWeb/servlet/HttpDispatcher/OIA0_0100/prompt";
      }

      this.$setCookie("link", `/${this.linkContent}`);
      window.location = `${this.schema}${this.linkContent}`;

      if (redirectUrl) {
        setTimeout(function () {
          window.location = redirectUrl;
        }, timeout);
      }
    },
  },
};
</script>

<style scoped>
.note-link {
  font-size: large;
  font-weight: bold;
  padding-top: 5px;
}
</style>
