const router = new VueRouter({
  mode: "hash",
  routes: [
    { path: "/" },
    {
      path: "/link/:param(.*)",
      name: "Param",
      component: LinkPage,
    },
    {
      path: "/access",
      name: "Access",
      component: AccessPage,
    },
    {
      path: "/agent",
      name: "Agent",
      component: AgentPage,
    },
  ],
});

Vue.use(VueRouter);

new Vue({
  router,
  render: (h) => h(App),
}).$mount("#app");
