import VueRouter from "vue-router";

import LinkPage from "@/views/Link.vue";
import AccessPage from "@/views/Access.vue";

// link page routes
const linkRoutes = [
  {
    path: "/",
    name: "Link",
    component: LinkPage,
  },
  {
    path: "/:param(.*)",
    name: "Param",
    component: LinkPage,
  },
];

// access page routes
const accessRoutes = [
  {
    path: "/",
    name: "Access",
    component: AccessPage,
  },
];

// create link router
const linkRouter = new VueRouter({
  mode: "hash",
  routes: linkRoutes,
});

// create access router
const accessRouter = new VueRouter({
  mode: "hash",
  routes: accessRoutes,
});

export { linkRouter, accessRouter };
